html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
  font-size: 100%;
}

* {
  font-family: "Lato", Helvetica, sans-serif;
  color: #333447;
  line-height: 2.5;
}

body {
  font-family: arial;
  font-size: 16px;
  line-height: 32px;
  background: red;
}

h1,
p {
  margin: 0;
}

h2 {
  font-size: 18px;
  line-height: 24px;
  font-weight: bold;
}

div.html5_logo {
  margin-left: 70px;
}

img {
  max-width: 100%;
}

table {
  max-width: 100%;
  text-align: center;
  margin: 0 auto;
}

.font-light {
  font-weight: 400;
}

.font-regular {
  font-weight: 500;
}

.font-heavy {
  font-weight: 900;
}

.left {
  text-align: left;
}

.right {
  text-align: right;
}

.center {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.justify {
  text-align: justify;
}

.hidden-sm {
  display: none;
}

.bg-red {
  background-color: red !important;
}

.bg-green {
  background-color: green !important;
}

.show-block {
  display: block !important;
}

.pb-6 {
  padding-bottom: 6px;
}

.zoom-in-one {
  width: 200px;
  text-align: center;
  animation: zoom-in-zoom-out 1s ease-in-out 1;
}

.zoom-in-three {
  width: 200px;
  text-align: center;
  animation: zoom-in-zoom-out 0.5s ease-in-out 2;
}

@keyframes zoom-in-zoom-out {
  0% {
    opacity: 0;
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.3, 1.3);
  }
  100% {
    opacity: 1;
    transform: scale(1, 1);
  }
}
#game_type {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 180px;
  height: 24px;
  line-height: 24px;
  content: "";
  background: skyblue;
  padding: 12px;
  border-radius: 12px 0px 0px 0px;
  text-align: left;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
}